* {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

*:before,
*:after {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

body {
    margin: 0;
    height: 100%;
    font-family: 'Spoqa Han Sans', sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.6;
    color: $text-medium-emphasis-color;
    background-color: $background-color;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

article,
aside,
details,
figcaption,
footer,
header,
hgroup,
main,
nav,
section,
summary {
    display: block;
}

.material-icons {
    font-size: 18px;
    vertical-align: middle;
}

h4 .material-icons {
    font-size: 48px;
    margin-bottom: 8px;
}

b,
strong {
    font-weight: 400;
    color: $text-high-emphasis-color;
}

dfn {
    font-style: italic;
}

small {
    font-size: 14px;
}

img {
    border: 0;
}

svg:not(:root) {
    overflow: hidden;
}

button,
input,
optgroup,
select,
