    margin: 0;
    font: inherit;
    color: inherit;
}

button {
    overflow: visible;
    border: none;
}

button,
select {
    text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button;
    cursor: pointer;
}

button[disabled],
html input[disabled] {
    cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
    padding: 0;
    border: 0;
}

input {
    line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box;
    padding: 0;
}

input[type="search"] {
    -webkit-box-sizing: content-box;
    -moz-box-sizing: content-box;
    box-sizing: content-box;
    -webkit-appearance: textfield;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

textarea {
    overflow: auto;
}

table {
    width: 100%;
    border-spacing: 0;
    background-color: transparent;
    margin-bottom: 20px;
    border: 1px solid $divider-color;
}

th {
    font-size: 18px;
    padding: 16px 12px;
    color: $text-high-emphasis-color;
    text-align: left;
}

td {
    padding: 8px 12px;
    text-align: left;
}

td:first-child {
    color: $text-high-emphasis-color;
}

tr:nth-child(even) {
    background: $background-variant-color
}

input,
button,
select,
textarea {
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
}

a {
    color: $text-high-emphasis-color;
    text-decoration: none;
    background: transparent;
    @include transition();
}

a:hover,
a:focus {
    color: $primary-color;
    text-decoration: none;
    outline: 0;
}

a:focus {
    outline: thin dotted;
    outline: 5px auto -webkit-focus-ring-color;
    outline-offset: -2px;
}

img {
    vertical-align: middle;
    max-width: 100% !important;
}

.img-rounded {
    border-radius: $border-radius;
}

.img-thumbnail {
    display: inline-block;
    max-width: 100%;
    height: auto;
    padding: 4px;
    line-height: 1.42857143;
    background-color: $text-high-emphasis-variant-color;
    border: $border;
    border-radius: $border-radius;
