input[type="search"] {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

input[type="radio"],
input[type="checkbox"] {
    position: absolute;
    opacity: 0;
    cursor: pointer;
}

.radio,
.checkbox {
    position: relative;
    padding-left: 28px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.radiomark {
    position: absolute;
    top: 4px;
    left: 4px;
    height: 18px;
    width: 18px;
    background-color: $divider-color;
    border-radius: 50%;
    @include transition();
}

.checkmark {
    position: absolute;
    top: 4px;
    left: 4px;
    height: 18px;
    width: 18px;
    background-color: $divider-color;
    border-radius: $border-radius;
    @include transition();
}

input:hover ~ .radiomark,
input:hover ~ .checkmark {
    background-color: $text-disabled-color;
}

input:checked ~ .radiomark,
input:checked ~ .checkmark {
    background-color: $primary-color;
}

input:checked ~ .radiomark:after,
input:checked ~ .checkmark:after {
    display: block;
}

.radiomark:after {
    content: "";
    position: absolute;
    display: none;
    top: 6px;
    left: 6px;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background: $text-high-emphasis-variant-color;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
    left: 7px;
    top: 3px;
    width: 5px;
    height: 10px;
    border: 2px solid $text-high-emphasis-variant-color;
    border-width: 0 2px 2px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
}

select[multiple],
select[size] {
    height: auto;
}

input[type="radio"]:focus,
input[type="checkbox"]:focus {
    outline: thin dotted;
    outline: 5px auto -webkit-focus-ring-color;
    outline-offset: -2px;
}

output {
    display: block;
    padding-top: 7px;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
}

.form-control {
    display: block;
    width: 100%;
    height: 44px;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
    color: $text-high-emphasis-color;
    background-color: transparent;
    background-image: none;
    border: $border;
    border-radius: $border-radius;
    @include transition();
}

.form-control:focus {
    border-color: $primary-color;
    outline: 0;
}

.form-control::-moz-placeholder {
    color: $text-disabled-color;
    opacity: 1;
}

.form-control:-ms-input-placeholder {
    color: $text-disabled-color;
}

.form-control::-webkit-input-placeholder {
    color: $text-disabled-color;
}

.form-control[disabled],
.form-control[readonly],
fieldset[disabled] .form-control {
    cursor: not-allowed;
    background-color: $divider-color;
    opacity: 1;
}

textarea.form-control {
    height: auto;
}

input[type="search"] {
    -webkit-appearance: none;
}

input[type="date"] {
    line-height: 34px;
}

.form-group {
    padding-top: 8px;
    padding-bottom: 8px;
}

.form-group .dropdown-toggle,
.form-group .dropdown-menu {
    text-align: left;
    width: 100%;
}

.radio label,
.checkbox label {
    display: inline;
    cursor: pointer;
}

.radio input[type="radio"],
.radio-inline input[type="radio"],
.checkbox input[type="checkbox"],
.checkbox-inline input[type="checkbox"] {
    float: left;
    margin-left: -20px;
}

.radio + .radio,
.checkbox + .checkbox {
    margin-top: -5px;
}

.radio-inline,
.checkbox-inline {
    display: inline-block;
    padding-left: 20px;
    margin-bottom: 0;
    vertical-align: middle;
    cursor: pointer;
}

.radio-inline + .radio-inline,
.checkbox-inline + .checkbox-inline {
    margin-top: 0;
    margin-left: 10px;
}

input[type="radio"][disabled],
input[type="checkbox"][disabled],
