::-moz-selection {
    color: $text-high-emphasis-variant-color;
    text-shadow: none;
    background: $text-medium-emphasis-color;
}

::selection {
    color: $text-high-emphasis-variant-color;
    text-shadow: none;
    background: $text-medium-emphasis-color;
}

.height-96 {
    height: 96px;
    line-height: 96px;
}

.lefted {
    text-align: left;
}

.centered {
    text-align: center;
}

.centered h3 {
  font-weight: 400;
}

.righted {
    text-align: right;
}

.pricing {
    text-align: center;
}

.podcast-ad {
    margin: 0;
}

.podcast-content {
    padding: 8px;
}

.podcast td {
    border: 0;
    padding: 0;
}

.headerwrap {
    padding-top: 40px;
    font-size: 20px;
}

.headerwrap h3 {
    color: $text-medium-emphasis-color;
    margin-top: 0px;
    margin-bottom: 16px;
}

.outro h1 {
    font-size: 36px;
    letter-spacing: -1px;
    margin-top: 10px;
    margin-bottom: 0;
}

.outro .btn {
    margin-bottom: 8px;
}

hr {
    border: 0;
    border-top: 1px solid $divider-color;
    margin: 24px 0;
}

.hr-none-md {
    margin: 0 0 32px 0;
}

.hr-sm {
    margin: 12px 0;
}

.hr-md-sm {
    margin: 24px 0 12px 0;
}

.hr-lg {
    margin: 40px 0;
}

.hr-lg-md {
    margin: 40px 0 24px 0;
}

.anchors li {
    margin: 4px 0;
}

.anchor {
    height: 0px;
}

.subscribe {
    margin-right: 8px;
}

.share i {
    padding-right: 15px;
    font-size: 18px;
}

.footer {
    font-size: 12px;
    margin-bottom: 24px;
}

.footer h6,
.footer h6 a {
    color: $text-high-emphasis-color;
    font-weight: 400;
}

.footer .nav > li {
    float: none;
}

.footer .nav > li > a,
.footer .nav > li > a:active,
.footer .nav > li > a:focus,
.footer a,
.footer a:active,
.footer a:focus {
    color: $text-medium-emphasis-color;
    font-size: 12px;
    padding: 0 0 4px 0;
    background-color: transparent;
}

iframe,
embed,
object {
    max-width: 100%;
}

#nav-icon {
    margin: 20px 16px 0 0;
    width: 24px;
    height: 24px;
    position: relative;
    cursor: pointer;
}

#nav-icon span {
    display: block;
    position: absolute;
    height: 2px;
    width: 100%;
    background: $text-medium-emphasis-color;
    border-radius: 2px;
    opacity: 1;
    left: 0;
    @include rotate-transition(0deg);
}

#nav-icon span:nth-child(1) {
    top: 0px;
}

#nav-icon span:nth-child(2) {
    top: 8px;
}

#nav-icon span:nth-child(3) {
    top: 16px;
}

#nav-icon.open span:nth-child(1) {
    @include rotate-transition(45deg);
    top: 8px;
}

#nav-icon.open span:nth-child(2) {
    width: 0%;
    left: 50%;
}

#nav-icon.open span:nth-child(3) {
    @include rotate-transition(-45deg);
    top: 8px;
}

.card {
    text-align: center;
    position: relative;
    border-radius: $border-radius;
    box-shadow: $box-shadow;
    margin: 24px 8px;
    padding: 8px 16px 16px 16px;
}

.card a {
    margin: 8px 0;
}

.card-text {
    text-align: left;
    margin-top: 16px;
}

.card-text p {
    margin-bottom: 16px;
}

.btn i,
li .svg-inline--fa {
    margin-right: 4px;
}

.product {
    width: 40px;
    margin-right: 8px;
}

.slideInUp {
    visibility: hidden;
    opacity: 0;
    -webkit-transition: .4s ease-in-out;
    -moz-transition: .4s ease-in-out;
    -o-transition: .4s ease-in-out;
    transition: .4s ease-in-out;
    -webkit-transform: translate3d(0, 24px, 0);
    transform: translate3d(0, 24px, 0);
}

.is-active .slideInUp {
    visibility: visible;
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
}

.slideInUp.slideInUp1 {
    @include delay(.2s);
}

.slideInUp.slideInUp2 {
    @include delay(.4s);
}

.slideInUp.slideInUp3 {
    @include delay(.6s);
}

.slideInUp.slideInUp4 {
    @include delay(.8s);
}

.slideInUp.slideInUp5 {
    @include delay(1s);
}

.slideInUp.slideInUp6 {
    @include delay(1.2s);
}

.slideInUp.slideInUp7 {
    @include delay(1.4s);
}
